/* 영문 필기체 */
.en {
    font-family: var(--sub-en);
    letter-spacing: 0px;
    line-height: 1;
    text-transform: none;
}

/* 영문 정자 */
.en2 {
    font-family: var(--sub-en2);
    letter-spacing: -0.5px;
    text-transform: none;
}

/* 한글 */
.ko {
    font-family: var(--main-ko);
    letter-spacing: -0.3px;
    line-height: 1;
}

